.bottom .slick-dots li {
	font-size: 30px !important;
	opacity: 0.5;
}
.bottom .slick-dots li.slick-active {
	opacity: 1;
}
.bottom .slick-dots li:hover {
	opacity: 1;
}
.custom1 .slick-prev::before {
	content: "";
	width: 20px;
	height: 20px;
	background: url(https://www.svgrepo.com/show/379969/chevrons-left-arrows.svg);
	display: block;
	background-size: 20px;
	background-repeat: no-repeat;
}
.custom1 .slick-next::before {
	content: "";
	width: 20px;
	height: 20px;
	background: url(https://www.svgrepo.com/show/379969/chevrons-left-arrows.svg);
	display: block;
	background-size: 20px;
	background-repeat: no-repeat;
	transform: rotate(180deg);
}

